<template>
  <div class="protocol">
    <!-- <Header @agree='agree' :agree='agree'></Header> -->
    <header class="protocolTitle">
      <div class="backClose">
        <img class="back" src="../../images/fan-hui.png" @click="back_" alt="">
        <img class="close" src="../../images/guan-bi.png" @click="nameOut" alt>
      </div>
      <p class="ellipsis">注册协议</p>
    </header>
    <div class="content bgfff pd40 rich-text" v-html="content"></div>
  </div>
</template>
<script>
import Header from "@/components/Header";
import checkurl from "@/utils/checkurl";
export default {
  components: {
    Header
  },
  data() {
    return {
      content: "",
      page_init: true
    };
  },
  created() {
    this.getAgreement();
  },
  methods: {
    getAgreement() {
      this.fetch.agreement({},(res => {
        this.content = res.data.content;
      }));
    },
    agree() {
      let path =
        localStorage.router_from_path === "/"
          ? "/login"
          : localStorage.router_from_path;
      this.$router.replace(path);
    },
    // 返回
    back_(){
      let path =
        localStorage.router_from_path === "/"
          ? "/login"
          : localStorage.router_from_path;
      this.$router.replace(path);
    },
     // 账号被挤掉
    nameOut() {
      if (sessionStorage.getItem("userPhone")) {
        sessionStorage.removeItem("userPhone");
        this.$router.replace("/");
      } else {
        this.$router.go(-1);
      }
    }
  }
};
</script>

<style lang="scss">
// .op0{
//   opacity: 0;
// }
// .op1{
//   opacity: 1;
// }
.protocol {
  padding-top: 0.2rem;
}
.rich-text img {
  width: 100%;
  height: auto;
}
.content {
  line-height: 0.42rem;
  letter-spacing: 0.01rem;
  color: #101d37;
}
.protocolTitle{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
  height: .88rem;
  line-height: .88rem;
  background-color: #fff;
  border-bottom: 1px solid #edeff5;
  box-sizing: border-box;
  padding: 0 .4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .ellipsis{ // 标题
    width: 60%;
    box-sizing: border-box;
    font-size: .36rem;
    letter-spacing: .01rem;
    color: #101d37;
    font-weight: bold;
  }
  .backClose{ // 返回
    width: 20%;
    box-sizing: border-box;
    margin-top: .1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding-right: .5rem;
    .back{
        width: .2rem;
        height: .34rem;
        margin-bottom: .14rem;
        margin-right: .4rem;
    }
    .close{
        width: .32rem;
        margin-bottom: .12rem;
    }
  }
}
</style>



